@import url(https://fonts.googleapis.com/css?family=Tillana);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
	position: relative;
	min-height: 100%;
}

body {
	background-color: #003d66;
	/* background-image: url('src/assets/backgound_textur_jpg.jpg'); */
	font-family: 'Tillana', 'Papyrus', cursive;
}

.footer {
	position: absolute;
	bottom: 0;
	width: 100%;
	height: 40px;
	/* Set the fixed height of the footer here */
	line-height: 40px;
	/* Vertically center the text there */
}

.max-size-thumbNail {
	max-width: 200px;
	max-height: 200px;
	padding: 10px;
}

.max-size-img {
	max-width: 400px;
	max-height: 400px;
	padding: 10px;
}

.text {
	text-align: center;
}

.about-text {
	color: #dabdab;
}
